<template>
  <div class="header">
    <div class="container">
      <div class="left-logo flex-row">
        <img src="../../assets/logo.jpg" alt="">
        <!-- <div class="title">{{ $store.state.title }}</div> -->
      </div>

      <ul class="right-menu">
          <li>公司简介</li>
          <li>关于我们</li>
          <li><i class="mdi icon icon-tel"></i> {{contact.tel1}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return {
      contact:{}
    }
  },
  mounted() {
        this.getdata();
    },
    methods: {
        getdata() {
            let _this = this;
            axios.get('static/data.json').then(function(res) {
                
                _this.contact = res.data.contact;

            }).catch(function(res) {
                console.log(res);
            });
        },
    }
}
</script>

<style lang="scss">
@import 'src/sass/into';
.header{
  height: $header-hight;
  background: $head-bg;
  box-shadow: $bs;
  
  .left-logo{
    line-height: 32px;
    float: left;
    @include clearfix;
    img{
      height: 80px;
      display: block;
      float: left;
    }
    .title{
      font-size: 24px;
      letter-spacing: 2px;
      font-weight: bold;
      display: inline-block;
      margin-left: 10px;
      color: $head-text;
    }
  }

  .right-menu{
    float: right;
    padding: 20px 0;
    li {
      padding: 0 10px ;
      line-height: 40px;
      float: left;
      font-size: 16px;
      margin-right: 30px;
      color: $text-dark;
      &:last-child{
        margin: 0;
        padding-right: 0;
        color: $red;
        font-weight: bold;
        font-size: 18px;
      }
      .mdi{
        font-size: 20px;
        // vertical-align: middle
      }
    }
  }
}
</style>



