<template>
  <div class="footer" >
    <div class="container">
      <el-row :gutter="20" class="links-wrap">
        <el-col :sm="12">
          <h5 class="group-title">关于我们</h5>
          <ul class="link-list inline">
            <li><router-link to="/">公司简介</router-link></li>
            <li><router-link to="/">主营业务</router-link></li>
            <li><router-link to="/">联系我们</router-link></li>
            <li><router-link to="/">更多</router-link></li>
          </ul>
        </el-col>
        
        <el-col :sm="12">
          <h5 class="group-title">联系我们</h5>
          <el-row>
            <el-col :sm="12">
              <ul class="link-list">
                <li><router-link to="/">热线电话：{{contact.tel1}}</router-link></li>
                <li><router-link to="/">联系电话：{{contact.tel2}}</router-link></li>
              </ul>
            </el-col>
            <el-col :sm="12">
              <div class="share-action">
                <el-popover ref="wxpop" placement="top-start" title="扫一扫关注" width="100" trigger="hover">
                  <img src="static/img/qcode.png" alt="" class="img-block">
                </el-popover>
                <div class="share weixin" v-popover:wxpop><i class="icon icon-weixin"></i></div>
                <div class="share qq"><i class="icon icon-qq-flat"></i></div>
                <div class="share weibo"><i class="icon icon-weibo"></i></div>
              </div>
            </el-col>
          </el-row>

        </el-col>
      </el-row>
      <div class="copyright">
        <p>ICP认证:  陕ICP备17013432号    Copyright © 2017 享互 All Rights Reserved. </p>
      </div>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
export default {
  data(){
    return {
      contact:{}
    }
  },
  mounted() {
        this.getdata();
    },
    methods: {
        getdata() {
            let _this = this;
            axios.get('static/data.json').then(function(res) {
                
                _this.contact = res.data.contact;

            }).catch(function(res) {
                console.log(res);
            });
        },
    }
}
</script>

<style lang="scss">
@import 'src/sass/into';
.footer{
  background-color: #2C313E;
  color: #969CAC;
  padding: 20px 0;
  a{
    color: inherit;
    @include transition(.3s);
    font-size: 14px;
    &:hover{
      color: $primary
    }
  }
  .links-wrap{
    padding-bottom: 20px;
    border-bottom: 1px solid rgba(#fff,.1);
  }
  .group-title{
    font-size: 18px;
    color: #787E8F;
    margin-bottom: 15px;
    font-weight: 600;
  }
  .link-list{
    li{
      padding: 5px 0;
      a{
        display: block;
        line-height: 2;
      }
    }
    &.inline{
      @include clearfix;
      li{
        float: left;
        margin-right: 15px;
      }
    }
  }
  .copyright{
    padding: 20px 0 0;
    text-align: center;
    line-height: 2;
    font-size: 12px;
    color: darken(#969CAC, 15%)
  }
  &.fixed-bottom{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
  }
}

</style>



