<template>
  <router-link class="item"
              :tag=" href==='' ? 'div' : 'a'"
              :to="href"
              :class="{ 'islink':href != '', 'has-line': hasLine, 'avatar-left':avatar, }">
    <slot name="check-left"></slot>
    <div class="img-left"
        v-if="imgSrc"
        :class="{ 'small':size=='small', 'large':size=='large' }"
        :style="{
          backgroundImage:'url('+ imgSrc +')',
          width: imgWidth + 'px',
          height: imgHeight + 'px'
        }">
      <!-- <img :src="imgSrc" alt=""> -->
    </div>
    <div class="icon-left"  v-if="icon">
      <i class="icon" :class="icon"></i>
    </div>
    <div class="item-body">
      <div class="item-body-top">
        <slot name="left"></slot>
        <div class="title" :class="{'line2':line=='2'}">{{title}} <slot name="title-inline"></slot></div>
        <slot name="right"></slot>
      </div>
      <div class="item-body-center" v-if="desc">
        <div class="line2">{{desc}}</div>
      </div>
      <slot name="desc"></slot>
      <slot name="bottom" class="item-body-footer"></slot>
    </div>
    <slot class="item-right" name="item-right"></slot>
  </router-link>
</template>

<script>

export default {

  props:{
    imgSrc:String,
    title: String,
    desc:  String,
    avatar: Boolean,
    line: String ,
    icon: String,
    hasLine:Boolean,
    imgWidth: String,
    imgHeight: String,
    href: {
      type:String,
      default:''
    },
    size:String
  },
  data(){
    return{

    }
  },
  methods:{

  }
}
</script>

<style lang="scss">
@import "src/sass/into";
.item{
  padding: 16px 0;
  position: relative;
  display: flex;
  flex-flow:row nowrap;
  justify-content: space-between;
  align-items: stretch;
  background-color: #fff;
  transition: all .3s;
  &:hover{
    .title{
      color: $primary !important;
    }
  }
  &.has-line{
    border-bottom: 1px solid rgba($border-light,.8)
  }
  .img-left{
    width: 90px;
    height: 60px;
    display: inline-block;
    margin-right: 10px;
    overflow: hidden;
    border-radius: 3px;
    background-color: #eee;
    position: relative;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    &.small{
      width: 60px;
      height: 40px;
    }
    &.large{
      width: 120px;
      height: 90px;
      align-self: flex-start;
    }
    img{
      width: 100%;
      height: 100%;
    }
  }
  .icon-left{
    width: 48px;
    height: 48px;
    display: flex;
    align-items: center;
    text-align: center;
    align-self: center;
    .icon{
      font-size: 32px;
      color: $primary;
      line-height: 1;
    }
  }

  .item-body{
    display: flex;
    flex-flow: column nowrap;
    flex: 1;
    line-height: 1.25;
    overflow: hidden;
    position: relative;
    .item-body-top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      .title{
        flex: 1;
      }
      small{
        font-size: 12px;
        color: #999;
      }
    }
    .item-body-center{
      margin-top: 5px;
      font-size: 12px;
      color: $text-light;
      line-height: 1.6;
      flex: 1
    }
    .title{
      @include ellipsis;
      font-size: 14px;
      margin: 0;
      color: $text-dark;
      transition: all .3s
    }
    p{
      width: 100%;
      font-size: 12px;
      color: $text-light;
      margin: 0;
      line-height: 1.6;
      @include ellipsis;
    }

    .item-body-footer{
      margin-top: 5px;
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      font-size: 12px;
      color: $text-light;
      span + span{
        margin-left: 10px;
      }
      .left{
        margin-right: 10px;
      }
      .center{
        flex: 1;
      }
      .right{
        margin-left: 10px;
      }
    }
  }
  .item-right{
    margin-left: 10px;
    align-self: center;
  }
  &.avatar-left {
    padding-top: 12px;
    padding-bottom: 12px;
    .img-left{
      @include circular(40px);
      align-self: flex-start;
    }
  }
  &.c-item{
    border-radius: $border-radius;
    .img-left{
      width: 80px;
      height: 80px;
      margin-right: 20px;
    }
  }
  &.card-style{
    padding: $spacing;
    background-color: #fff;
    border-radius: $border-radius;
    box-shadow: $bs;
    margin-bottom: $spacing;
  }
  &.border-style{
    padding: $spacing;
    border: 1px solid $border-light;
    border-radius: 2px;
    margin-bottom: $spacing;
  }
}

</style>



