<template>
    <router-link class="g-item" :to="href" :class="{'img-style':imgStyle}">
        <div class="media" :style="{backgroundImage:'url('+ thumSrc +')'}">
            <img :src="thumSrc" alt="title" v-if="imgStyle">
            <span class="state" v-if="label">{{label}}</span>
        </div>
        <div class="title" v-if="title">{{title}}</div>
    </router-link>
</template>

<script>

export default {
    name: 'gItem',
    props:{
        href: {
          type : String,
          default : '/'
        },

        thumSrc:{
          type : String,
          default : 'static/img/image.jpg'
        },
        imgStyle:{
          type : Boolean,
          default : false
        },
        title: String,
        label: String
    },
    data(){
        return{

        }
    },
    methods:{

    }
}
</script>

<style lang="scss">
@import "src/sass/into";

.g-item{
	// background: #fff;
	border-radius: $border-radius;
	padding-bottom: 10px;
	margin-bottom: 10px;
	display: block;
	.media{
		transition: all .3s;
		border-radius: $border-radius;
		overflow: hidden;
		position: relative;
		display: block;
		background-color: #eee;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover;
		height: 200px;
		img{
			width: 100%;
			display: block;
			transition: all .3s
		}
		.state{
			position: absolute;
			left: 0;
			top: 0;
			border-radius: 5px 0 5px 0;
			background: rgba($red,.8);
			font-size: 14px;
			padding: 6px 15px;
            color: #fff;
            &.bg-red{
                background: rgba($red,.8);
            }
		}
	}
	.author{
		position:relative;
		padding: 5px 10px 10px 70px;
		color: $text-light;
		font-size: 12px;
    	display: block;
		.avatar-box{
		  border: 2px solid #fff;
			position: absolute;
			left: 10px;
			top: -20px;
		}
	}
	.title{
		text-align: center;
	  display: block;
		font-size: 14px;
		color: $text-dark;
		font-weight: normal;
		line-height: 1.6;
		@include ellipsis;
		padding: 8px 0;
	}
	.bottom{
		display: flex;
		align-items: center;
		font-size: 12px;
		color: $text-light;
		.price{
			font-size: 14px;
		}
	}
	&:hover{
		.media{
			box-shadow: 0 4px 10px rgba(#000,.2);
			transform: translateY(-1px);
			img{
				transform: scale(1.2) rotate(5deg)
			}
		}
		.title{
			color: $red;
		}
	}
	&.img-style{
		.media{
			background: none !important;
			height: auto !important;
		}
	}
}


</style>



